.user-profile-menu {
    padding: 5px;
    color: #1D1E1F;
    box-shadow: 0px 0px 16px rgba(105, 153, 247, 0.18);

    .user-profile-menu__rating {
        margin-right: 10px;
    }

    .user-profile-menu__profile {
        margin-right: 30px;
        max-width: 180px;
        overflow: hidden;
        white-space: nowrap;
    }

    .user-profile-menu__close {
        width: 22px;
        height: 22px;
        &:before,
        &:after {
            height: 2px;
        }
    }
}